body {
  margin: 0;
  font-family: monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: black !important; */
}

input {
  outline: none;
  background-color:black;
  width: 48%;
  color: gray !important;
  border-radius: 5px;
  padding: 3px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div.iframe-link {
  color: #64AD27;
}

div.iframe-link:hover{
  color: #448D07;
}

div.normal {
  color: #1C8173;
}

div.link {
  color: #337ab7;
  text-decoration: none;
}

div.link:hover {
  color: #135a97;
}




iframe {
  position: fixed;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

input:focus {
  color: black !important;
  background-color: #202020;
}

.MAGNOLIAL_seperator {
  background-color: transparent;
  border: none;
  font-size: 16;
  border-radius: 5px;
  margin-left: 3px;
  margin-right: 3px;
  padding-top: 3px;
  padding-bottom: 3px;
  outline: none;
  display: inline-block;
  text-align: center;
  width: 3%;
  min-width: 20px
}

.MAGNOLIAL_seperator:focus {
  background-color: #202020;
}

.MAGNOLIAL {
  padding-top: 20px;
  padding-bottom: 30px;
  font-family: monospace;
  /* background-color: black; */
  color: #1C8173;
  display:flex;
  justify-content:center;

  height: 100%;
}

.MAGNOLIAL_decoration {
  width: 15px;
  font-size: 20px !important;
  margin-right: 6px;
  margin-top: 6px;
  vertical-align: top;
  display: inline-block;
  cursor: pointer;
}

.MAGNOLIAL .MAGNOLIAL_decoration.MAGNOLIAL_completed {
  color: gray;
}

.MAGNOLIAL_error {
  color: red;
  padding-left: 5px;
}


.MAGNOLIAL p {
  color: #F6E7BA;
  font-size: 22;
  margin: 0;
  padding: 0;
  padding-left: 22px;
}

.MAGNOLIAL ul {
  margin: 0;
  margin-left: 9px;
  padding-left: 26px;
  border-left-width: 1px;
  border-left-style: solid;
  border-left-color: #202020;
}

.MAGNOLIAL li {
  list-style: none;
}

.MAGNOLIAL_hidden {
  display: none;
}

.MAGNOLIAL div.MAGNOLIAL_ce_wrapper {
  display: inline-block;
  position: relative;
  padding: 0;
  margin: 0;
  width: 90%;
  cursor: pointer;
}

.MAGNOLIAL div.MAGNOLIAL_ce_top {
  position:absolute;
  top: 0;
  left: 0;
  z-index: 1;
  min-width: 100px;
  min-height: 22px;
}

.MAGNOLIAL div.MAGNOLIAL_ce_bottom {
  z-index: 0;
  min-width: 100px;
  min-height: 22px;
}

.MAGNOLIAL div.MAGNOLIAL_ce {
  display: inline-block;
  outline: none;
  border: none;
  padding: 0;
  padding-left: 4px;
  padding-right: 4px;
  margin: 0;
  font-weight: 500;
  font-size: 22px;
  font-family: monospace;
  min-height: 31.4333px;
  white-space: pre-wrap;       /* css-3 */
  white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
  white-space: -pre-wrap;      /* Opera 4-6 */
  white-space: -o-pre-wrap;    /* Opera 7 */
  word-wrap: break-word;       /* Internet Explorer 5.5+ */
}

.MAGNOLIAL div.MAGNOLIAL_ce.MAGNOLIAL_focused {
  background-color: #135a97;
  color:#F6E7BA;
  border-radius: 3px;
  padding-left: 4px;
  padding-right: 4px;
}


.MAGNOLIAL div.MAGNOLIAL_ce_top.MAGNOLIAL_focused {
  background-color: #135a97;
  color:white;
}

.MAGNOLIAL div.MAGNOLIAL_ce.MAGNOLIAL_completed {
  text-decoration: line-through;
  color: gray;
}

.MAGNOLIAL div.MAGNOLIAL_ce.MAGNOLIAL_transparent {
  color:transparent!important;
  background-color:transparent!important;
}

.MAGNOLIAL div.breadcrumb-wrapper {
  margin: 0;
  padding-bottom: 10px;
  font-size: 22px;
  display: inline-block;
}

.MAGNOLIAL div.title {
  margin: 0;
  padding-bottom: 10px;
  display: inline-block;
  width: 350px;
}

.MAGNOLIAL h1 div.MAGNOLIAL_ce {
  font-weight: 900;
  font-size: 22px;
  color: #F6E7BA!important;
}

.MAGNOLIAL .breadcrumb-text {
  font-weight: 100;
  color: #807861;
  cursor: pointer;
}

.MAGNOLIAL .breadcrumb-text:hover {
  color: #A09881
}

.MAGNOLIAL .breadcrumb-sym {
  font-weight: 100;
  padding-left: 5px;
  padding-right: 5px;
  display: inline-block;
  color: #DB9E31;

}
